import alfrid, { GL } from 'alfrid';
import { onAREnd, onARStart } from 'signals';

import Assets from '../../Assets';
import Config from '../Config';
import fs from 'shaders/laptop.frag';
// import fs from 'shaders/laptopFallback.frag';
import gsap from 'gsap';
import vs from 'shaders/laptop.vert';

// import vs from 'shaders/laptopFallback.vert';

const definesToString = function (defines) {
  let outStr = '';

  for (const def in defines) {
    	if (defines[def]) {
    		outStr += `#define ${def} ${defines[def]}\n`;
    	}
  }

  return outStr;
};

const tempMat4 = mat4.create();
const tempVec3 = vec3.create();

export class ViewProduct extends alfrid.View3D {
  constructor(scene, parent, options = {}) {
    const useIBL = GL.FLOAT !== GL.gl.UNSIGNED_BYTE;

    const defines = {
      USE_TEX_LOD: GL.getExtension('EXT_shader_texture_lod') ? 1 : 0,
      USE_IBL: useIBL,
      HAS_BASECOLORMAP: 1,
      HAS_NORMALMAP: 0,
      HAS_EMISSIVEMAP: 0,
      HAS_OCCLUSIONMAP: 0,
    };

    const defineStr = definesToString(defines);
    // console.log(defineStr);
    const _vs = `${defineStr}\n${vs}`;
    const _fs = `${defineStr}\n${fs}`;

    super(_vs, _fs, options);

    this.useIBL = useIBL;
    this.darkness = 0;
    this.darkOverlay = 0;
    this.lightOverlay = 0;
    this.scene = scene;
    this.mParent = parent._matrix;
    this.parentAR = new alfrid.Object3D();
    this.m = mat4.create();
    this.screenObj = new alfrid.Object3D();

    this.ready = false;
    this.setScale(Config.productScale);

    parent.anchorY = (Config.productDimensions.y * Config.productScale) / 2;
    parent.anchorZ = (Config.productDimensions.z * Config.productScale) / 4;

    this.posCamera = vec3.create();
    
    this.posCamera[0] = -2.4
    this.posCamera[1] = 1.5
    this.posCamera[2] = -3.8
    
    onARStart.add(this.onARStart.bind(this));
    onAREnd.add(this.onAREnd.bind(this));

    // gui.add(this.parentAR, 'rotationX', -Math.PI, Math.PI).step(0.1);
    // gui.add(this.parentAR, 'rotationY', -Math.PI, Math.PI).step(0.1);
    // gui.add(this.parentAR, 'rotationZ', -Math.PI, Math.PI).step(0.1);
    // gui.add(this.screenObj, 'rotationX', -Math.PI / 2, 0).step(0.1);
  }

  _init(options = {}) {
    this.alpha = 1;

    // dimensions computer before scale
    // 56
    // 37.5
    // 36.7

    this.cube3d = new alfrid.Object3D();
    this.cube = alfrid.Geom.cube(1, 1, 1);


    

    // this.screenObj.rotationX = 0; // close the screen

    // gui.add(this.cube3d, 'x', -100, 100).step(0.1);
    // gui.add(this.cube3d, 'y', -100, 100).step(0.1);
    // gui.add(this.cube3d, 'z', -100, 100).step(0.1);
    // gui.add(this.cube3d, 'scaleX', 0, 100).step(0.1);
    // gui.add(this.cube3d, 'scaleY', 0, 100).step(0.1);
    // gui.add(this.cube3d, 'scaleZ', 0, 100).step(0.1);

    const version = options.version || 'a3';
    this.studioRadiance = Assets.get('studio_radiance');
    this.irr = Assets.get('irr');

    this.screenMetallicmap = Assets.get(`${version}_screen_metallic`);
    this.screenColormap = Assets.get(`${version}_screen_basecolor`);
    this.screenRoughnessmap = Assets.get(`${version}_screen_roughness`);

    this.bodyMetallicmap = Assets.get(`${version}_body_metallic`);
		this.bodyColormap = Assets.get(`${version}_body_basecolor`);
    this.bodyRoughnessmap = Assets.get(`${version}_body_roughness`);
    

    this.bottom = Assets.get(`xiaomi_${version}_bottom_piece`);
    this.top = Assets.get(`xiaomi_${version}_screen`);
    this.baseColor = [1, 1, 1];

    this.shader.bind()
  }

  onARStart() {
    this.isAR = true;
    this.screenObj.rotationX = 0; // close the screen
    this.setScale(Config.productScaleAR);
    mat4.translate(this.m, this.m, [0, 0.02, 0]);
    this.updateMatrix(this.m);
  }

  reset() {
    this.setRotationEuler(0, 0, 0);
    this.setPosition(0, 0, 0);
  }

  onAREnd() {
    this.isAR = false;
    this.reset();
    this.setScale(Config.productScale);
    mat4.identity(this.m);
    this.updateMatrix(this.m);
  }

  toggleScreen(on, duration = 1, cb, ease = 'sine.out') {
    gsap.to(this.screenObj, duration, {
      rotationX: on ? -Math.PI / 1.8 : 0,
      ease,
      onComplete: cb ? cb() : null,
    });
  }

  animateIn(cb) {
    this.toggleScreen(true, 1);
    gsap.to(this, 1, {
      alpha: 1,
      ease: 'sine.out',
      onComplete: cb ? cb() : null,
    });
  }

  place() {
    this.ready = true;

    gsap.to(this.parentAR, 1, {
      rotationX: 0,
      rotationY: 0,
      rotationZ: 0,
      y: 0,
      ease: 'sine.out',
    });

    gsap.to(this, 1, {
      lightOverlay: 0,
      ease: 'sine.out',
    });
  }

  render() {
    this.shader.bind();
    
    this.shader.uniform('uLightOverlay', 'float', this.lightOverlay);
    this.shader.uniform('uDarkOverlay', 'float', this.darkOverlay);
    this.shader.uniform('uDarkness', 'float', this.darkness);
    this.shader.uniform('uMetallicMap', 'uniform1i', 0);
    this.bodyMetallicmap.bind(0);
    
		this.shader.uniform('uBRDFMap', 'uniform1i', 1);
		Assets.get('brdfLUT').bind(1);

    if (this.useIBL) {
      this.shader.uniform('uRadianceMap', 'uniform1i', 3);
      this.shader.uniform('uIrradianceMap', 'uniform1i', 2);

      this.studioRadiance.bind(3);
      this.irr.bind(2);
    }


		this.shader.uniform('uColorMap', 'uniform1i', 4);
		this.bodyColormap.bind(4);
		this.shader.uniform('uRoughnessMap', 'uniform1i', 5);
		this.bodyRoughnessmap.bind(5);

    

    if (this.isAR) {
      const transform = this.scene.cameraAR.transform;
      const view = transform.matrix;

      tempVec3[0] = view[12];
      tempVec3[1] = view[13];
      tempVec3[2] = view[14];

      mat4.copy(tempMat4, this._matrix);
      mat4.multiply(tempMat4, tempMat4, this.parentAR._matrix);
      
    } else {
      vec3.add(tempVec3, this.scene.controlsProduct.position, this.posCamera);
      mat4.multiply(tempMat4, this.mParent, this._matrix);
      mat4.multiply(tempMat4, tempMat4, this.parentAR._matrix);
    }

		this.shader.uniform('uCameraPos', 'vec3', tempVec3);

    GL.rotate(tempMat4);
    GL.draw(this.bottom);

    this.screenMetallicmap.bind(0);
		this.screenColormap.bind(4);
    this.screenRoughnessmap.bind(5);


    mat4.multiply(tempMat4, tempMat4, this.screenObj._matrix);
    GL.rotate(tempMat4);
    GL.draw(this.top);
  }
}
