import '../../css/main.css';

import { onSpotClicked, onSpotHover, onSpotOut } from 'signals';

import SceneApp from './SceneApp';
import Signal from 'mini-signals';
import assetsList from './asset-list';
import { preloadBuild } from 'utils/preload';

export default class WebGL {
  constructor() {
    this.scene = null;
    this.onSpotClicked = new Signal();
    this.onSpotHover = new Signal();
    this.onSpotOut = new Signal();

    onSpotClicked.add(this._onSpotClicked.bind(this));
    onSpotHover.add(this._onSpotHover.bind(this));
    onSpotOut.add(this._onSpotOut.bind(this));
  }

  _onSpotClicked(data) {
    this.onSpotClicked.dispatch(data);
  }

  _onSpotHover(data, pos) {
    this.onSpotHover.dispatch(data, pos);
  }

  _onSpotOut() {
    this.onSpotOut.dispatch();
  }

  // called from main app
  startControls() {
    if (this.scene) this.scene.resume();
  }

  stopControls() {
    if (this.scene) this.scene.pause();
  }

  start() {
    if (this.scene) this.scene.start();
  }

  stop() {
    if (this.scene) this.scene.stop();
  }

  init(options) {
    options.assets = assetsList;
    preloadBuild(options).then(() => {
      this.scene = new SceneApp(options, this);
    }, (err) => {
      console.log('err', err);
    });
  }
}
