import Config from '../Config';
import { StateDefault } from './StateDefault';

export class StateARDetail extends StateDefault {
  begin({ hotspot }) {
    this.scene._vHud.stop();
    this.product = this.scene._vProduct;
    this.hotspots = this.scene._vHotSpots;

    this.product.darkOverlay = 1;

    // this.hotspots.stop();
    this.hotspots.select(hotspot);
    
    this.closePos = [window.innerWidth - 20, 20];
    this.details = this.scene._vARDetail;
    this.details.setData(hotspot);
    this.details.start();

    this._onARDown = this.onARDown.bind(this);
    this.webXRSystem = this.scene.webXRSystem;
    this.webXRSystem.on('onDown', this._onARDown);
  }

  onARDown(pos) {
    const h = window.innerHeight - window.innerHeight * Config.ARDetails.white.height;
    
    if (pos[1] < h) {
      this.details.animateOut().then(() => {
        this.hotspots.unselect();
        this.scene.statesSystem.set('ARMain');
      });
    }
  }

  end() {
    this.scene._vHud.start();
    this.details.stop();
    this.webXRSystem.off('onDown', this._onARDown);
    this.product.darkOverlay = 0;
  }
}