import Assets from '../Assets';
import alfrid from 'alfrid';
export class Sprite extends alfrid.Object3D {
  constructor(textureName) {
    super();

    this.uvOffset = vec2.create();
    this.uvScale = vec2.create();
    this.alpha = 1;

    if (textureName) {
      this.setTexture(textureName)
    }
  }

  setTexture(name) {
    const asset = Assets.get(name);

    vec2.set(this.uvScale, 1, 1);
    this.rotation = 0;

    if (asset.frame) {
      this.texture = Assets.get(asset.texture.name);
      vec2.set(this.uvOffset, asset.frame.x, asset.frame.y);
      vec2.set(this.uvScale, asset.frame.w, asset.frame.h);
    }
    else {
      this.texture = asset;
    }
    this.ratio = this.uvScale[0] / this.uvScale[1];

  }

  updateShader(shader, bindTexture = false) {
    if (!this.texture) return;

    shader.uniform('uAlpha', 'float', this.alpha);
    shader.uniform('uScale', 'vec2', [this.scaleX, this.scaleY])
    shader.uniform('uPosition', 'vec2', [this.x, this.y])
    shader.uniform('uUVOffset', 'vec2', this.uvOffset)
    shader.uniform('uUVScale', 'vec2', this.uvScale)

    // optional because all the sprites (for now) use the same texture
    if (bindTexture) this.texture.bind(0);
  }
}