function applyMatrix4(vec3, e) {
  const x = vec3[0]; const y = vec3[1];
  const z = vec3[2];

  const w = 1 / (e[3] * x + e[7] * y + e[11] * z + e[15]);

  vec3[0] = (e[0] * x + e[4] * y + e[8] * z + e[12]) * w;
  vec3[1] = (e[1] * x + e[5] * y + e[9] * z + e[13]) * w;
  vec3[2] = (e[2] * x + e[6] * y + e[10] * z + e[14]) * w;
}

