// Assets.js

import alfrid, { GLCubeTexture, GLTexture, Mesh, ObjLoader } from 'alfrid';

const Assets = {};
let _assets = [];
let assets;
const frames = {};
const textures = [];

const getAsset = function (id) {
  return assets.find((a) => a.id === id).file;
}

const getExtension = function (mFile) {
  const ary = mFile.split('.');

  return ary[ary.length - 1];
}

Assets.init = function (mAssets, assetsToLoad) {
  assets = mAssets;
  const hdrCubemaps = {};

  _assets = assetsToLoad.map((o) => {
    const ext = getExtension(o.url);

    const file = getAsset(o.id);
    let texture;

    switch (ext) {
      case 'jpg':
      case 'png':
        texture = new GLTexture(file);
        textures.push(texture)

        return {
          id: o.id,
          file: texture,
        };
        break;

      case 'hdr':
        const cubemapName = o.id.split('_')[0];

        texture = alfrid.HDRLoader.parse(file);

        const oAsset = {
          id: o.id,
          file: texture,
        };

        if (!hdrCubemaps[cubemapName]) {
          hdrCubemaps[cubemapName] = [];
        }

        hdrCubemaps[cubemapName].push(oAsset);

        return oAsset;

        break;
      case 'dds':
        texture = GLCubeTexture.parseDDS(file);

        return {
          id: o.id,
          file: texture,
        };
        break;

      case 'obj':
        const mesh = ObjLoader.parse(file);

        return {
          id: o.id,
          file: mesh,
        }
        break;
      case 'json':
        if (file.frames) {
          // is spritesheets

          const size = file.meta.size;

          for (const sprite in file.frames) {
            const fr = file.frames[sprite];
            const frame = fr.frame;

            frames[sprite.split('.')[0]] = {
              frame: {
                x: Math.floor(frame.x) / size.w,
                y: 1. - Math.floor(frame.y) / size.h - Math.floor(frame.h) / size.h,
                w: Math.floor(frame.w) / size.w,
                h: Math.floor(frame.h) / size.h,
              },
              texture: {
                name: file.meta.image.split('.')[0],
                size: file.meta.size,
              },
            }
          }
        }

        return {
          id: `${o.id}_json`,
          file,
        }

        break;
      default:
    }
  });

  for (const sprite in frames) {
    _assets.push({
      id: sprite,
      file: frames[sprite],
    });
  }

  for (const s in hdrCubemaps) {
    if (hdrCubemaps[s].length == 6) {

      const ary = [
        Assets.get(`${s}_posx`),
        Assets.get(`${s}_negx`),
        Assets.get(`${s}_posy`),
        Assets.get(`${s}_negy`),
        Assets.get(`${s}_posz`),
        Assets.get(`${s}_negz`),
      ];

      const texture = new alfrid.GLCubeTexture(ary);

      _assets.push({
        id: s,
        file: texture,
      })
    }
  }

  if (_assets.length > 0) {
  }
}

Assets.destroy = (gl) => {
  for (let i = 0; i < textures.length; i++) {
    gl.deleteTexture(textures[i]._texture);
  }

  textures.length = 0;
}

Assets.get = function (mId) {
  return _assets.find((a) => a.id === mId).file;
}

export default Assets;
