import alfrid, { GL } from 'alfrid';
import Assets from '../../Assets';
import gsap from 'gsap';

import vs from 'shaders/clouds.vert';
import fs from 'shaders/clouds.frag';

export class ViewCloudPlane extends alfrid.View3D {
    constructor() {
        super(vs, fs);
        this.mesh = alfrid.Geom.plane(6, 6, 1);
        this.setPosition(0.2, 1, -2);
        this.shader.bind();
		this.shader.uniform('gradient', 'uniform1i', 1);
        this._texture = Assets.get('cloud');
        this._gradientTexture = Assets.get('cloudlut');
        this._texture.wrapS = GL.REPEAT;
        this._texture.wrapT = GL.REPEAT;
    }

    render() {
        this.shader.bind();
        this._texture.bind(0);
        this._gradientTexture.bind(1);
        this.shader.uniform('uTime', 'float', performance.now() / 10000)
        GL.rotate(this._matrix);
        GL.draw(this.mesh);
    }
}