import { EventEmitter } from 'events';
import { GL } from 'alfrid';
import { map } from 'utils';

export class WebXRSystem extends EventEmitter {
  constructor() {
    super();

    this.width = 0;
    this.height = 0;
    this.hasView = false;

    // keep these listeners because they're greedy, and we don't want to emit 60fr per second
    this.listeners = { onMove: null, onRender: null }; 

    this.mousePos = vec2.create();

    this._onDown = this.onDown.bind(this);
    this._onUp = this.onUp.bind(this);
    this._onSessionEnd = this.onSessionEnd.bind(this);

    this.reset();
  }

  reset() {
    if (this.currentSession) {
      this.clearSignals(this.currentSession);
      this.emit('onAREnd')
    }

    GL.gl.bindFramebuffer(GL.gl.FRAMEBUFFER, null)

    this.currentSession = null;
    vec3.set(this.mousePos, 0, 0, 0);
    this.down = false;
    // this.refId = null;
    this.hasView = false;

    this.xrImmersiveRefSpace = null;
    this.xrInlineRefSpace = null;
    this.hitTestSource = false;
    this.inputSource = null;

    this.reticleMatrix = null;
    this.active = false;
    
    this.hitTestSource = null;
  }

  addSession(session) {
    session.addEventListener('selectstart', this._onDown);
    session.addEventListener('selectend', this._onUp);
    session.addEventListener('end', this._onSessionEnd);

    session.updateRenderState({ baseLayer: new XRWebGLLayer(session, GL.gl) });
    session.requestReferenceSpace('viewer').then((referenceSpace) => {
      session.requestHitTestSource({ space: referenceSpace }).then((source) => {
        this.hitTestSource = source;
      });
    });

    session.requestReferenceSpace('local').then((refSpace) => {
      this.xrImmersiveRefSpace = refSpace;
      this.active = true;
      this.emit('onARStart')
      this.refId = session.requestAnimationFrame(this.onXRFrame.bind(this));
    });

    this.currentSession = session;
  }

  onSessionEnd() {
    this.refId = this.currentSession.cancelAnimationFrame(this.refId);
    this.reset();
  }

  clearSignals(session) {
    session.removeEventListener('selectstart', this._onDown);
    session.removeEventListener('selectend', this._onUp);
    session.addEventListener('end', this._onSessionEnd);
  }

  stop() {
    if (this.currentSession) {
      this.currentSession.end();
    }
  }

  onDown(event) {
    this.down = true;
    this.inputSource = event.inputSource
    this.processMouse();

    this.emit('onDown', this.mousePos);
  }

  onUp() {
    this.down = false;
    this.inputSource = null;

    this.emit('onUp', this.mousePos);
  }

  onMove() { // pos is an array
    if (this.listeners.onMove) this.listeners.onMove(this.mousePos);
  }

  processMouse() {
    if (!this.inputSource) return;
    const axes = this.inputSource.gamepad.axes;

    this.mousePos[0] = map(axes[0], -1, 1, 0, window.innerWidth);
    this.mousePos[1] = map(axes[1], -1, 1, 0, window.innerHeight);
  }

  onXRFrame(t, frame) {
    this.hasView = false;
    

    if (this.down && this.listeners.onMove) {
      this.processMouse()
      this.onMove();
    }

    const session = frame.session;
    const pose = frame.getViewerPose(this.xrImmersiveRefSpace);
    const layer = session.renderState.baseLayer;

    GL.gl.bindFramebuffer(GL.gl.FRAMEBUFFER, layer.framebuffer);

    session.requestAnimationFrame(this.onXRFrame.bind(this));

    if (pose) {
      if (this.hitTestSource) {
        this.hits = frame.getHitTestResults(this.hitTestSource);

        if (this.hits.length) {
          const poseHit = this.hits[0].getPose(this.xrImmersiveRefSpace);

          this.reticleMatrix = poseHit.transform.matrix;
        }
        else {
          this.reticleMatrix = null;
        }
      } else {
        this.reticleMatrix = null;
      }

      for (const view of pose.views) {
        this.hasView = true;
        const { width, height } = layer.getViewport(view);

        if ((width !== this.width || height !== this.height)) {
          this.emit('onResize', { width, height });
        }

        if (this.listeners.onRender) this.listeners.onRender(view, session); // keep this one as a unique listener function so it's faster
      }
    }

    

    
  }

  resize(w, h) {
    // to make sure we resize if the main webgl changes size
    // it would retrigger it from the onXRFrame function above
    this.width = w;
    this.height = h;
  }
}
