import alfrid, { GL, GLShader, Geom, Ray } from 'alfrid';
import { get2dPos, intersectionRayMesh } from 'utils';
import { onAREnd, onARStart, onSpotHover } from 'signals';

import Assets from '../../Assets';
import Config from '../Config';
import fs from 'shaders/marker.frag';
import fsMask from 'shaders/mask.frag';
import vs from 'shaders/marker.vert';
import vsMask from 'shaders/mask.vert';

const tempVec3 = vec3.create();
const tempVec3b = vec3.create();
const tempMat4 = mat4.create();
const tempMat4b = mat4.create();
// const tempMat4Identity = mat4.create();
// const vec3Plane1 = vec3.create();
// const vec3Plane2 = vec3.create();
// const vec3Plane3 = vec3.create();

export class ViewHotSpots extends alfrid.View3D {
  constructor(scene, parent, spots) {
    super(vs, fs);

    this.ray = new Ray([0, 0, 0], [0, 0, -1]);
    this.time = 0;
    this.popupHotspot = null;

    this.selectedId = null;
    this.animating = 0;

    this.shaderPlane = new alfrid.GLShader(vsMask, fsMask)
    this.parent = parent;
    this.mParent = parent._matrix;
    const s = 0.1;

    this.cube = Geom.cube(s * 1.25, s * 1.25, s * 1.25); // s * 1.25 to give it a bit more room for the click

    this.mesh = Assets.get('disk');
    // this.mesh = Geom.sphere(s, 15);

    this.scene = scene;
    this.active = false;
    this.orbitalControl = scene.orbitalControl;
    this.dataSpots = [];
    this.spots = [];

    this.spotsAR = null;
    this.spotsDefault = null;

    if (spots) this.addSpots(spots);

    onARStart.add(this.onARStart.bind(this));
    onAREnd.add(this.onAREnd.bind(this));

    // const oSettings = { minFilter: GL.LINEAR, magFilter: GL.LINEAR };
    // this.fboDepth = new alfrid.FrameBuffer(GL.width, GL.height, oSettings);
  }

  start() {
    this.active = true;
  }

  stop() {
    this.active = false;
  }

  // updated from StateARMain when the computer is placed!
  updateMatrices(matrix) {
    mat4.getTranslation(tempVec3, matrix);
    this.setPosition(tempVec3[0], tempVec3[1], tempVec3[2])

    this.setRotationEulerFromMatrix(matrix);
  }

  getSpots(isAr) {
    const defaultScale = 0.04; // scale of the product when creating the points
    const scale = (isAr ? Config.productScaleAR : Config.productScale) / defaultScale; // scale of the product when creating the points

    const spots = [];

    for (let i = 0; i < this.dataSpots.length; i++) {
      const spot = this.dataSpots[i];
      const zoom = spot.zoom * scale;
      const offset = [...spot.offset];
      const target = [...spot.target];
      const center = [...spot.center];

      offset[0] *= scale;
      offset[1] *= scale;
      offset[2] *= scale;

      target[0] *= scale;
      target[1] *= scale;
      target[2] *= scale;

      center[0] *= scale;
      center[1] *= scale;
      center[2] *= scale;

      spots.push({ zoom, offset, target, center, label: spot.label, rx: spot.rx, ry: spot.ry, indexImg: spot.indexImg, fitWidth: spot.fitWidth })
    }

    return spots;
  }

  addSpots(spotsData) {
    if (!spotsData.length) return;

    this.dataSpots = spotsData;
    this.createSpots(false);
  }

  createSpots(isAR) {
    if (isAR && !this.spotsAR) this.spotsAR = this.getSpots(true);
    else if (!isAR && !this.spotsDefault) this.spotsDefault = this.getSpots(false);

    const spots = isAR ? this.spotsAR : this.spotsDefault;
    const posOffset = [];
    const ids = [];

    for (let i = 0; i < spots.length; i++) {
      const existingSpot = this.spots[i];
      const currentSpot = spots[i];

      const name = currentSpot.name || `Annotation ${i + 1}`;
      const pos = this.orbitalControl.getPosition(currentSpot.rx, currentSpot.ry, currentSpot.zoom);
      const matrix = existingSpot ? existingSpot.matrix : mat4.create(); // used only for the hit testing

      pos[0] += currentSpot.offset[0];
      pos[1] += currentSpot.offset[1];
      pos[2] += currentSpot.offset[2];

      posOffset.push(currentSpot.target);

      if (existingSpot) mat4.identity(matrix);
      mat4.translate(matrix, matrix, currentSpot.target)

      if (existingSpot) { // over optimising... probably useless!
        existingSpot.target = currentSpot.target;
        existingSpot.pos = pos;
        existingSpot.matrix = matrix;
        existingSpot.data = currentSpot;
      }
      else {
        const s = 0.05;
        const cube = Geom.cube(s * 1.25, s * 1.25, s * 1.25); // KEEP FOR DEBUG

        ids.push([i]);

        this.spots.push({
          delay: Math.random() * 500,
          id: i,
          target: currentSpot.target,
          pos,
          name,
          vertices: this.cube.vertices,
          matrix,
          parentMatrixAR: this._matrix,
          parentMatrices: [this._matrix, this.mParent],
          mesh: cube, // KEEP FOR DEBUG
          data: currentSpot,
        });
      }
    }

    // if (ids.length) {
    // this.mesh.bufferInstance(ids, 'aID', 1);
    // }
    // this.mesh.bufferInstance(posOffset, 'aPosOffset');

    this.object3DPlane = new alfrid.Object3D();
    // this.object3DPlane.rotationY = Math.PI;

    const scale = isAR ? Config.productScaleAR : Config.productScale;

    const width = Config.productDimensions.x * scale;
    const height = Config.productDimensions.y * scale;

    if (!this.plane) {
      this.plane = alfrid.Geom.cube(1.01, 1.0, 0.2);
    }
    this.object3DPlane.rotationX = Math.PI / 2 - Math.PI / 1.8 + Math.PI;
    this.object3DPlane.y = 0.67 * scale / Config.productScale;
    this.object3DPlane.z = -0.16 * scale / Config.productScale;

    this.object3DPlane.scaleX = width;
    this.object3DPlane.scaleY = 1.44 * scale / Config.productScale;
    this.object3DPlane.scaleZ = 0.03 * scale / Config.productScale;

    // gui.add(this.object3DPlane, 'scaleY', height - 0.1, height + 0.1).step(0.01)
    // gui.add(this.object3DPlane, 'scaleZ', 0, 0.5).step(0.01)
    // gui.add(this.object3DPlane, 'z', -2, 2).step(0.01)
    // gui.add(this.object3DPlane, 'y', -2, 2).step(0.001)
  }

  select(hotspot) {
    this.selectedId = hotspot.id;
    this.animating = 1;
  }

  unselect() {
    this.popupHotspot = null;
    this.selectedId = null;
    this.animating = 0;
  }

  reset() {
    this.popupHotspot = null;
    this.setRotationEuler(0, 0, 0);
    this.setPosition(0, 0, 0);
    this._update();
    this.stop();
    this.createSpots();
  }

  
  onARStart() {
    this.popupHotspot = null;
    this.isAR = true;
    this.stop(); // hide the spots
    this.createSpots(true); // resize them
  }

  onAREnd() {
    this.isAR = false;
    this.reset();
  }
  

  isVisible(spot, mat, matPlane) {
    // probably some room for optimisations here?
    vec3.transformMat4(tempVec3, spot.target, mat);
    vec3.copy(tempVec3b, tempVec3, mat);
    
    const cameraPos = this.scene.camera.position;
    
    if(this.isAR) {
      const transform = this.scene.cameraAR.transform;
      const view = transform.matrix;
      cameraPos[0] = view[12];
      cameraPos[1] = view[13];
      cameraPos[2] = view[14];
    }

    this.ray.origin = cameraPos;

    vec3.subtract(tempVec3, tempVec3, cameraPos);
    vec3.normalize(tempVec3, tempVec3);
    this.ray.direction = tempVec3;

    const hit = intersectionRayMesh(this.ray, this.plane.vertices, matPlane);

    if (hit) {
      const distCamSphere = vec3.distance(cameraPos, tempVec3b);
      const distCamScreen = vec3.distance(cameraPos, hit);

      return distCamSphere <= distCamScreen;
    }

    return true;
  }

  showInfos(hotspot) {
    this.popupHotspot = hotspot;
  }

  render() {
    if (!this.active || !this.mesh) return;

    if (this.isAR) {
      mat4.copy(tempMat4, this._matrix);
    }
    else {
      mat4.multiply(tempMat4, this._matrix, this.mParent);
    }

    mat4.multiply(tempMat4b, tempMat4, this.object3DPlane._matrix);

    this.time++;

    this.shader.bind();

    GL.disable(GL.DEPTH_TEST);
    GL.disable(GL.CULL_FACE);

    this.shader.uniform('uAnimating', 'float', this.animating)
    this.shader.uniform('uTime', 'float', this.time)
    this.shader.uniform('uScale', 'float', (this.scene.webXRSystem && this.scene.webXRSystem.active) ? 0.3 * 0.2 : 1 * 0.2)

    GL.rotate(tempMat4);

    for (let i = 0; i < this.spots.length; i++) {
      const spot = this.spots[i];
      
      const isVisible = this.isVisible(spot, tempMat4, tempMat4b)

      if (!isVisible) continue;

      if (this.popupHotspot && this.popupHotspot.id === spot.id) {
        vec3.transformMat4(tempVec3, spot.target, tempMat4);

        const pos = get2dPos(tempVec3);
        onSpotHover.dispatch(spot.data, pos);
      }

      this.shader.uniform('uAR', 'float', (this.scene.webXRSystem && this.scene.webXRSystem.active) ? 1 : 0)
      this.shader.uniform('uSelected', 'float', this.selectedId === spot.id ? 1 : 0);
      this.shader.uniform('uPosOffset', 'vec3', spot.target);
      this.shader.uniform('uDelay', 'float', spot.delay);

      GL.draw(this.mesh);
    }

    GL.enable(GL.CULL_FACE);
    GL.enable(GL.DEPTH_TEST);

    // this.shaderPlane.bind();
    // mat4.multiply(tempMat4, tempMat4, this.object3DPlane._matrix);
    // GL.rotate(tempMat4);
    // GL.draw(this.plane);

    // GL.enable(GL.CULL_FACE);

    // KEEP FOR DEBUG
    //   this.shaderPlane.bind();
    //   for (let i = 0; i < this.spots.length; i++) {
    //     const s = this.spots[i];
    //     const mesh = s.mesh;
    //     const matrix = s.matrix;

    //     const parentMatrices = s.parentMatrices;

    //     if (parentMatrices) {
    //       if (parentMatrices) {
    //         mat4.identity(tempMat4);
    //         for (let k = 0; k < parentMatrices.length; k++) {
    //           mat4.multiply(tempMat4, parentMatrices[k], tempMat4)
    //         }

    //       }
    //       mat4.multiply(tempMat4, tempMat4, matrix)
    //     }

  //     GL.rotate(tempMat4);
  //     GL.draw(mesh);
  //   }
  //   GL.enable(GL.DEPTH_TEST);
  }
}
