// ViewCube.js

import alfrid, { GL } from 'alfrid';
import { onAREnd, onARStart } from 'signals';

import Assets from '../../Assets';
import Config from '../Config';
import { Sprite } from 'helpers';
import fs from 'shaders/sprite.frag';
import { getDistOptim } from 'utils';
import vs from 'shaders/sprite.vert';

let ind = 0;

const tempVec3 = vec3.create();

export class ViewHud extends alfrid.View3D {
  constructor(scene) {
    super(vs, fs);
    this.scene = scene;

    this.mainTexture = Assets.get('hotspots_img');
    this.mainTexture.texture.minFilter = GL.gl.NEAREST;
    this.mainTexture.texture.magFilter = GL.gl.NEAREST;
    this.active = false;

    this.shader.bind();
    this.shader.uniform('texture', 'uniform1i', 0);

    this.backIcon = new Sprite('back-icon');
    this.imgText = new Sprite();

    this.mesh = alfrid.Geom.plane(1, 1, 1);

    this.imageSteps = [
      'tutorial-1',
      'tutorial-2',
      'tutorial-3',
    ]
    this.currentStep = 0;

    onARStart.add(this.onARStart.bind(this));
    onAREnd.add(this.onAREnd.bind(this));

    this._onARDown = this.onARDown.bind(this)
  }

  onARStart() {
    this.scene.webXRSystem.on('onDown', this._onARDown);
  }

  onAREnd() {
    this.scene.webXRSystem.off('onDown', this._onARDown);
  }

  onARDown(pos) {
    if (!this.active) return;

    const dist = getDistOptim(pos[0], pos[1], 40, 40);

    if (dist < 20 * 20) {
      this.scene.webXRSystem.stop();
    }

  }

  start() {
    this.active = true;
  }

  stop() {
    this.active = false;
  }

  setState(state) {
    this.currentStep = state

    if (this.imageSteps[this.currentStep]) {
      this.imgText.setTexture(`tutorial-${this.currentStep + 1}`);

      const width = this.width * 0.5;
      const height = width / this.imgText.ratio;

      this.imgText.setScale(width, height, 1);
      this.message = true;
    } else {
      this.message = false;
    }
  }

  render() {
    // if (!this.active) return;

    // const posCamera = this.scene.touchSystem.positionCamera;
    // const dirCamera = this.scene.touchSystem.dirCamera;

    // const dist = 1.5;

    // this.x = posCamera[0] + dirCamera[0] * dist;
    // this.y = posCamera[1] + dirCamera[1] * dist;
    // this.z = posCamera[2] + dirCamera[2] * dist;

    GL.disable(GL.DEPTH_TEST);
    this.shader.bind();
    this.mainTexture.bind(0);

    if (this.message) {
      this.imgText.updateShader(this.shader);
      GL.draw(this.mesh);
    }

    this.backIcon.updateShader(this.shader);
    GL.draw(this.mesh);


    GL.enable(GL.DEPTH_TEST);
  }

  resize(w, h) {
    this.width = w;
    this.height = h;

    // this.bg.setScale(window.innerWidth, window.innerHeight, 1);

    let height = h * Config.ARDetails.white.height;

    this.backIcon.setScale(40, 40, 1);
    this.backIcon.setPosition(-window.innerWidth / 2 + 40, window.innerHeight / 2 - 60, 0);

    this.imgText.setPosition(0, -window.innerHeight / 5, 0);
  }
}
