import { GL, Ray } from 'alfrid';
import { getMouse, intersectionRayMesh } from 'utils';

import { EventEmitter } from 'events';

const tempVec3 = vec3.create();
const tempVec3b = vec3.create();
const mInverseViewProj = mat4.create();
const tempMat4 = mat4.create();


export class TouchSystem extends EventEmitter {
  constructor(camera, cameraAR) {
    super();

    this.listeners = { onDown: null };
    this.mouse = vec2.create();
    this.camera = camera;
    this.cameraAR = cameraAR;
    this.positionCamera = vec3.create();
    this.dirCamera = vec3.create();
    this.dirCameraDebug = vec3.create();
    this.listeners = { onDown: null, onMove: null, onUp: null };
    this.targets = [];
    this.hits = [];
    this.ray = new Ray([0, 0, 0], [0, 0, -1]);

    this._onDown = this.onDown.bind(this);
    this._onMove = this.onMove.bind(this);
  }

  connect() {
    this.connected = true;
    if (GL.isMobile) {
      window.addEventListener('touchstart', this._onDown);
    } else {
      window.addEventListener('mousedown', this._onDown);
      window.addEventListener('mousemove', this._onMove);
    }
	}
  
	disconnect() {
    this.connected = false;
    if (GL.isMobile) {
      window.removeEventListener('touchstart', this._onDown);
    } else {
      window.removeEventListener('mousedown', this._onDown);
      window.removeEventListener('mousemove', this._onMove);
    }
  }

  generateRay(x, y) {    
		const mx = (x / window.innerWidth) * 2.0 - 1.0;
    const my = - (y / window.innerHeight) * 2.0 + 1.0;
		this.camera.generateRay([mx, my, 0], this.ray);
  }
  
  onDown(e) { // only called from non AR (event is on window)
    this.onMove(e, true);
    // const o = getMouse(e, this.mouse);
    // let hit = null;

    // this.generateRay(o[0], o[1]);
    // for (let i = 0; i < this.targets.length; i++) {
    //   const target = this.targets[i];
      
    //   const matrix = target.getMatrix ? target.getMatrix() : (target.matrix || target._matrix);
    //   const parentMatrices = target.parentMatrices;

    //   if (parentMatrices) {
    //     mat4.identity(tempMat4);
    //     for (let k = 0; k < parentMatrices.length; k++) {
    //       mat4.multiply(tempMat4, parentMatrices[k], tempMat4)
    //     }
        
    //     mat4.multiply(tempMat4, tempMat4, matrix)
    //   }
    //   // hit = intersectionRayMesh(this.ray, target.vertices, parentMatrix ? tempMat4 : matrix);

    //   hit = intersectionRayMesh(this.ray, target.vertices, parentMatrices ? tempMat4 : matrix);

    //   // if(hit && this.listeners.onDown) {
    //   if(hit) {
		// 		this.emit('onDown', { hit, target, index: i });

    //     return;
    //   }
    // }
  }

  onMove(e, isDown) {
    if (GL.isMobile && !isDown) return;

    const o = getMouse(e, this.mouse);
    let hit = null;

    this.generateRay(o[0], o[1]);
    for (let i = 0; i < this.targets.length; i++) {
      const target = this.targets[i];
      
      const matrix = target.getMatrix ? target.getMatrix() : (target.matrix || target._matrix);
      const parentMatrices = target.parentMatrices;

      if (parentMatrices) {
        mat4.identity(tempMat4);
        for (let k = 0; k < parentMatrices.length; k++) {
          mat4.multiply(tempMat4, parentMatrices[k], tempMat4)
        }
        
        mat4.multiply(tempMat4, tempMat4, matrix)
      }
      // hit = intersectionRayMesh(this.ray, target.vertices, parentMatrix ? tempMat4 : matrix);

      hit = intersectionRayMesh(this.ray, target.vertices, parentMatrices ? tempMat4 : matrix);

      // if(hit && this.listeners.onDown) {
      if(hit) {
        if (isDown) {
          console.log('here')
          this.emit('onDown', { hit, target, index: i });
        } else if (!GL.isMobile){
          this.emit('onHover', { hit, target, index: i });
        }

        return;
      }
    }

    this.emit('onOut');
  }

  setTargets(targets) {
    this.targets = targets;
  }

  generateRayAR(x, y) {
    const mx = (x / window.innerWidth) * 2.0 - 1.0;
    const my = -(y / window.innerHeight) * 2.0 + 1.0;

    const screenPos = [mx, my, 0];

    const transform = this.cameraAR.transform;
    const viewInverse = transform.inverse.matrix;
    const view = transform.matrix;

    this.positionCamera[0] = view[12];
    this.positionCamera[1] = view[13];
    this.positionCamera[2] = view[14];

    const proj = this.cameraAR.projectionMatrix;

    mat4.multiply(mInverseViewProj, proj, viewInverse);
    mat4.invert(mInverseViewProj, mInverseViewProj);
    vec3.transformMat4(this.dirCamera, screenPos, mInverseViewProj);
    vec3.sub(this.dirCamera, this.dirCamera, this.positionCamera);
    vec3.normalize(this.dirCamera, this.dirCamera);

    const newPos = tempVec3; 
    const dist = tempVec3b;
    
    vec3.copy(newPos, this.positionCamera);
    vec3.copy(dist, this.dirCamera);
    vec3.scale(dist, this.dirCamera, 1 * 1.5);
    vec3.add(newPos, newPos, dist);

    vec3.copy(this.dirCameraDebug, newPos);

    this.ray.origin = this.positionCamera;
    this.ray.direction = this.dirCamera;
  }


  hit(pos, loopThrough) { // only called from AR world
    this.generateRayAR(pos[0], pos[1]);
    this.hits.length = 0;
    let hit = null;

    for (let i = 0; i < this.targets.length; i++) {
      const target = this.targets[i];

      const matrix = target.getMatrix ? target.getMatrix() : (target.matrix || target._matrix);
      const parentMatrix = target.parentMatrixAR;

      if (parentMatrix) {
        mat4.multiply(tempMat4, parentMatrix, matrix)
      }
      hit = intersectionRayMesh(this.ray, target.vertices, parentMatrix ? tempMat4 : matrix);

      if (hit) {
        this.hits.push({ target, position: hit, index: i });

        if (!loopThrough) return this.hits;
      }
    }

    return this.hits;
  }

  start() {
  }

  stop() {

  }
}
