import Config from '../Config';
import { StateDefault } from './StateDefault';
import { applyMatrix4 } from 'utils';

const tempVec3 = vec3.create();

export class StateARScan extends StateDefault {
  begin() {
    this.webXRSystem = this.scene.webXRSystem;
    this.reticle = this.scene._vReticle;
    this.product = this.scene._vProduct;
    this.product.lightOverlay = 1;

    this._onARDown = this.onARDown.bind(this);
    this.webXRSystem.on('onDown', this._onARDown);

    this.scene._vHud.start();
    this.scene._vHud.setState(0);
  }

  onARDown() {
    this.product.place();
    this.scene.statesSystem.set('ARMain');
  }

  end() {
    this.webXRSystem.off('onDown', this._onARDown);
    this.reticle.stop();
  }

  update() {
    if (!this.webXRSystem.hasView) return; // check because the AR maybe hasn't detected any view yet!

    const retMat = this.webXRSystem.reticleMatrix;
    
    if (retMat) {
      this.reticle.setReticleMatrix(retMat);
      this.reticle.start();

      vec3.set(tempVec3, 0, 0, 0);
      applyMatrix4(tempVec3, this.reticle.getMatrix());
      this.product.setPosition(tempVec3[0], tempVec3[1], tempVec3[2]);
      
      this.product.parentAR.y += (Config.productDimensions.y - this.product.parentAR.y) * 0.1;
      this.product.parentAR.rotationX += (0.6 - this.product.parentAR.rotationX) * 0.1;
      this.product.parentAR.rotationY += (-0.6 - this.product.parentAR.rotationY) * 0.1
      this.product.parentAR.rotationZ += (-0.2 - this.product.parentAR.rotationZ) * 0.1



      this.product.screenObj.rotationX = -0.2;
      // mat4.fromRotation(this.product.m, Math.PI / 2, [0, 0, 1]);
      // this.product.updateMatrix(this.product.m);
    }
    else {
      this.reticle.stop();
      // render computer in front of the cam
      vec3.set(tempVec3, 0, 0, -2);
      applyMatrix4(tempVec3, this.scene.viewMatrix);
      this.product.setPosition(tempVec3[0], tempVec3[1], tempVec3[2]);

      this.product.parentAR.y += (0 - this.product.parentAR.y) * 0.1;
      this.product.parentAR.rotationX += (0 - this.product.parentAR.rotationX) * 0.1;
      this.product.parentAR.rotationY += (0 - this.product.parentAR.rotationY) * 0.1
      this.product.parentAR.rotationZ += (0 - this.product.parentAR.rotationZ) * 0.1
    }

    this.scene.touchSystem.generateRayAR(0, 0);
    const angle = Math.atan2(this.scene.touchSystem.ray.direction[2], this.scene.touchSystem.ray.direction[0]);

    this.product.rotationY = -angle - Math.PI / 2 - Math.PI / 10;
  }

}