import { getCameraDistanceFitX, getScales } from 'utils';

import Config from '../Config';
import { GL } from 'alfrid';
import { StateDefault } from './StateDefault';
import gsap from 'gsap';

export class StateIntro extends StateDefault {
  begin() {
    this.scene.buttonAR.hide();
    this.tick = 0;
    this.product = this.scene._vProduct;
    this.product.darkness = 1;
    this.controls = this.scene.orbitalControl;
    this.controls.rx.value = 0.3;
    this.controls.ry.value = -0.05;

    this.radius = 5.10; 
    this.controls.lock(true)
    
    if (GL.isMobile) {
      this.radius = getCameraDistanceFitX(window.innerWidth, (Config.productDimensions.x * Config.productScale), (Config.productDimensions.z * Config.productScale) * (GL.isMobile ? 0.8 : 0.5), this.scene.camera, this.controls);
    }

    this.controls.radius.setTo(this.radius);


    const { scaleY } = getScales(this.scene.camera, this.controls, 1, 1, 0)

    this.rotX = 0.1;
    this.rotZ = 0.05;
    this.y = scaleY / 6

    const duration = 1.5;

    gsap.delayedCall(1, () => {
      this.product.toggleScreen(true, duration * 1.2, null, 'circ.inout');
      gsap.to(this, duration, {
        y: 0,
        rotX: 0.46,
        rotZ: 0.27,
        ease: 'circ.inout',
        onComplete: () => {
          const dur = Config.fast ? 0.5 : 2;

          gsap.delayedCall(dur, () => {
            this.scene.statesSystem.set('introToHome');
          });
        }
      })
    });
    

    // setTimeout(()=>{
    // }, 2000)
  }

  update() {
    const speed = 1;

    this.tick += speed;

    this.controls.rx.value = 0.3 + Math.cos(this.tick / 100) * 0.005;
    this.controls.ry.value = -0.05 + Math.sin(this.tick / 120) * 0.005;
    this.controls.radius.value = this.radius + Math.cos(this.tick / 100) * 0.1;
    this.product.y = this.y + Math.cos((this.tick) / 60) * 0.08;
    this.product.rotationX = this.rotX + Math.cos((this.tick) / 40) * 0.03;
    this.product.rotationZ = this.rotZ + Math.cos((this.tick) / 40) * 0.03;

  }
}