import { GL } from 'alfrid';
import { StateDefault } from './StateDefault';

export class StateHome extends StateDefault {
  begin() {
    if (GL.isMobile) {
      this.scene.buttonAR.show();
    }
    this.scene._vHotSpots.reset(); // reset hotspots sizes
    this.scene._vHotSpots.start(); // show the hotspots

    this.scene.orbitalControl.disconnect();
    this.scene.controlsProduct.connect();
  }

  update() {
  }
}