import alfrid, { GL } from 'alfrid';
import Assets from '../../Assets';
import gsap from 'gsap';

import vs from 'shaders/flow.vert';
import fs from 'shaders/flow.frag';

export class ViewFlowPlane extends alfrid.View3D {
    constructor() {
        super(vs, fs);
        this._texture = Assets.get('flow');
        this.mesh = alfrid.Geom.plane((this._texture.width / this._texture.height) * 5, 5, 1);
        this.setPosition(0, 0.5, -2)
        this.shader.bind();
        this.shader.uniform('uInverse', 'float', (this._texture.width / this._texture.height));
    }

    render() {
        this.shader.bind();
        this._texture.bind(0);
        // this.rotationZ += performance.now()/100000000;
        this.shader.uniform('uTime', 'float', performance.now() / 10000)
        GL.rotate(this._matrix);
        GL.draw(this.mesh);
    }
}