import { getDistOptim, getMouse } from 'utils';

import { OrbitalControlTween } from 'helpers';
import { StateDefault } from './StateDefault';
import { onSpotClicked } from 'signals';

export class StateDetail extends StateDefault {
  constructor(scene) {
    super(scene);

    this._onDown = this.onDown.bind(this);
    this._onMove = this.onMove.bind(this);
    this._onUp = this.onUp.bind(this);
  }

  begin(data = {}) {
    this.down = false;
    this.currentSpot = data.currentSpot;
    // this.scene.orbitalControl.lock(true); // lock the orbital control
    this.scene._vHotSpots.stop(); // hide the other spots

    this.radius = this.scene.orbitalControl.radius.value;

    OrbitalControlTween.tween({
      centerX: this.currentSpot.center[0], centerY: this.currentSpot.center[1], centerZ: this.currentSpot.center[2],
      offsetX: this.currentSpot.offset[0], offsetY: this.currentSpot.offset[1], offsetZ: this.currentSpot.offset[2],
      rx: this.currentSpot.rx, ry: this.currentSpot.ry, radius: this.currentSpot.zoom,
      ease: 'sine.out', duration: 0.8,
    });

    onSpotClicked.dispatch(this.currentSpot);

    this.addListeners();
  }

  removeListeners() {
    this.scene.containerHTML.removeEventListener('touchstart', this._onDown, { passive: false });
    this.scene.containerHTML.removeEventListener('mousedown', this._onDown);
    this.scene.containerHTML.removeEventListener('mouseup', this._onUp);
    this.scene.containerHTML.removeEventListener('touchmove', this._onMove, { passive: false });
    this.scene.containerHTML.removeEventListener('mousemove', this._onMove);
  }

  addListeners() {
    this.scene.containerHTML.addEventListener('touchstart', this._onDown, { passive: false });
    this.scene.containerHTML.addEventListener('mousedown', this._onDown);
    this.scene.containerHTML.addEventListener('mouseup', this._onUp);
    this.scene.containerHTML.addEventListener('touchmove', this._onMove, { passive: false });
    this.scene.containerHTML.addEventListener('mousemove', this._onMove);
  }

  onDown(e) {
    this.down = true;
    this.pos = getMouse(e);
  }

  onMove(e) {
    if (!this.down) return;

    const pos = getMouse(e);
    const dist = getDistOptim(this.pos[0], this.pos[1], pos[0], pos[1]);

    if (dist > 20 * 20) {
      this.scene.statesSystem.set('home');
    }
  }

  onUp(e) {
    this.down = false;
  }

  end({ to }) {
    this.down = false;
    this.removeListeners();

    OrbitalControlTween.tween({
      offsetX: 0, offsetY: 0, offsetZ: 0,
      radius: this.radius,
      centerX: 0, centerY: 0, centerZ: 0, ease: 'sine.out', duration: 0.75 });
  }

  render() {
  }
}
