// ViewReticle.js

import alfrid, { GL } from 'alfrid';

import Assets from '../../Assets';
import fs from 'shaders/reticle.frag';
// import { getGeometryFaces } from 'utils';
import vs from 'shaders/reticle.vert';

const tempMat4 = mat4.create();

export class ViewReticle extends alfrid.View3D {
  constructor() {
    super(vs, fs);

    this.active = false;
    this.mesh = alfrid.Geom.plane(0.5, 0.5, 1, 'xz');
    this.texture = Assets.get('reticle');
    
    this.m = mat4.create();
    // this.rotationX = Math.PI / 2;
    // this.setScale(0.2);
  }

  start() {
    this.active = true;
  }

  stop() {
    this.active = false;
  }

  getMatrix() {
    mat4.multiply(tempMat4, this.m, this._matrix); // probably no need for that multiplication

    return this.m;
  }

  setReticleMatrix(m) {
    mat4.copy(this.m, m);
  }

  render() {
    if (!this.active) return;

    this.shader.bind();
    this.shader.uniform('texture', 'uniform1i', 0);
    this.texture.bind(0);
    mat4.multiply(tempMat4, this.m, this._matrix);
    GL.rotate(tempMat4);
    GL.draw(this.mesh);
  }
}
