import alfrid, { GL, Scene } from 'alfrid';
import { createARLayer, createContainer, resize } from 'utils';
import { onAREnd, onARStart, onSpotClicked, onSpotOut } from 'signals';

import Config from './Config';
import { OrbitalControlTween } from 'helpers';
import { StatesSystem } from './systems/StatesSystem';
import { TouchSystem } from './systems/TouchSystem';
import { ViewARBlackBG } from './views/ViewARBlackBG';
import { ViewARDetail } from './views/ViewARDetail';
import { ViewCloudPlane } from './views/ViewCloudPlane';
import { ViewFlowPlane } from './views/ViewFlowPlane';
import { ViewHotSpots } from './views/ViewHotSpots';
import { ViewHud } from './views/ViewHud';
import { ViewProduct } from './views/ViewProduct';
import { ViewReticle } from './views/ViewReticle';
import { WebXRSystem } from './systems/WebXRSystem';

class SceneApp extends Scene {
  constructor(options = {}) {
    GL.enableAlphaBlending();
    createContainer(options); // will also set the options.container
    
    super(options);


    window.scene = this;
  }

  _init(options) {
    super._init(options);
    this.options = options;
    this.buttonAR = createARLayer(this, options.containerButtonAR); // will also set the options.container
    this.active = true;
    this.previousState = {}

    this.positionDebug = vec3.create(); // debug
    this.positionCenterDebug = vec3.create(); // debug

    this.cameraAR = new alfrid.CameraPerspective();
    this.cameraAR.setPerspective(45 * Math.PI / 180, GL.aspectRatio, 0.1, 100);
    this.cameraOrtho.ortho(-window.innerWidth / 2, window.innerWidth / 2, window.innerHeight / 2, -window.innerHeight / 2, 0, 30);

    this.product3D = new alfrid.Object3D();
    this.controlsProduct = new alfrid.OrbitalControl(this.product3D, window, 5);
    this.controlsProduct.rx.limit(0, Math.PI / 8); // will be limited in some part of the state (eg for mobile)
    this.controlsProduct.lockZoom();

    OrbitalControlTween.init(this.orbitalControl);
    this.statesSystem = new StatesSystem(this);
    this.containerHTML = options.container;
    this.hotSpots = [];
    this.tick = 0;
  }

  _initViews(options) {
    // helpers
    this._bCopy = new alfrid.BatchCopy();
    this._bAxis = new alfrid.BatchAxis();
    this._bDots = new alfrid.BatchDotsPlane();
    this._bBall = new alfrid.BatchBall();
    this._bBall2 = new alfrid.BatchBall();
    this._bSky = new alfrid.BatchSky(50, 10);

    // views
    this._vReticle = new ViewReticle();
    this._vProduct = new ViewProduct(this, this.product3D, options);
    this._vARDetail = new ViewARDetail(this);
    this._vARBlackBG = new ViewARBlackBG(this);
    this._vHotSpots = new ViewHotSpots(this, this.product3D, Config.editor.points);
    this._vHud = new ViewHud(this);
    this._vClouds = new ViewCloudPlane();
    this._vFlow = new ViewFlowPlane();
    this._vHotSpots.setParentMatrix(this._vProduct._matrix);

    this._onSpotHover = this.onSpotHover.bind(this);
    this._onSpotOut = this.onSpotOut.bind(this);

    this.touchSystem = new TouchSystem(this.camera, this.cameraAR);
    this.touchSystem.on('onDown', this.onSpotClicked.bind(this));
    this.touchSystem.on('onHover', this._onSpotHover);
    this.touchSystem.connect();
    this.touchSystem.setTargets(this._vHotSpots.spots);

    this.resize();
    this.statesSystem.set('intro');
  }

  addSession(session) {
    if (!this.webXRSystem) {
      this.webXRSystem = new WebXRSystem();
      this.webXRSystem.listeners.onRender = this.renderScene.bind(this);
      this.webXRSystem.on('onResize', ({ width, height }) => resize(width, height, null, null, true));
      this.webXRSystem.on('onARStart', this.onARStart.bind(this));
      this.webXRSystem.on('onAREnd', this.onAREnd.bind(this));
    }
    else {
      this.webXRSystem.reset();
    }

    this.webXRSystem.addSession(session);
  }

  onARStart() {
    onARStart.dispatch();
    this.statesSystem.set('ARScan', {}, true);
  }

  onAREnd() {
    onAREnd.dispatch();
    this.statesSystem.set('home', {}, true);
    this.resize();
  }

  onSpotClicked({ target }) {
    onSpotClicked.dispatch(target.data);
  }
 
  onSpotHover({ target }) {
    if (!this._vHotSpots.active) return;

    this._vHotSpots.showInfos(target);

    this.touchSystem.off('onHover', this._onSpotHover);
    this.touchSystem.on('onOut', this._onSpotOut);
  }

  onSpotOut() {
    onSpotOut.dispatch();
    this.touchSystem.on('onHover', this._onSpotHover);
    this.touchSystem.off('onOut', this._onSpotOut);
    this._vHotSpots.showInfos(null);
  }

  renderScene(view, session) {
    if (view) {
      GL.setMatrices(this.cameraAR);

      const transform = view.transform;
      const viewInverseMatrix = transform.inverse.matrix;
      const projectionMatrix = view.projectionMatrix;

      this.cameraAR.setFromViewProj(viewInverseMatrix, projectionMatrix);
      this.cameraAR.transform = transform;
      
    }
    
    
    GL.clear(0, 0, 0, 0);

    
    this.statesSystem.update();
    
    // this._bAxis.draw();
    // this._bDots.draw();
    
    
    
    /**
     * DEBUG
     */
    // this._bBall.draw(this.touchSystem.positionCamera, [0.025, 0.025, 0.025], [0.0, 1.0, 1.0]);
    // this._bBall2.draw(this.touchSystem.dirCameraDebug, [0.025, 0.025, 0.025], [0.0, 0.0, 1.0]);
    // this._bBall.draw(this.positionDebug, [0.025, 0.025, 0.025], [0.0, 1.0, 1.0]);
    // this._bBall2.draw(this.positionCenterDebug, [0.025, 0.025, 0.025], [0.0, 0.0, 1.0]);

    this._vReticle.render();

    if (this.isAR && this._vARDetail.active) {
      this._vARBlackBG.render();
    }
    else if (!this.isAR) {
      if (this.options.mainExperience) {
        this._vClouds.render();
      } 
      else {
        this._vFlow.render();
      }
    }

    this._vProduct.render();
    this._vHotSpots.render();

    if (this.isAR) {
      GL.setMatrices(this.cameraOrtho);

      if (this._vHud.active) {
        this._vHud.render();
      }
      else if (this._vARDetail.active) {
        this._vARDetail.render();
      }

      GL.setMatrices(this.cameraAR);
    }
  }

  render() {
    if (!this.webXRSystem || (this.webXRSystem && !this.webXRSystem.active)) {
      // this.statesSystem.update();
      this.renderScene();
    }
  }

  resize(w, h) {
    resize(null, null, this.containerHTML.clientWidth, this.containerHTML.clientHeight);
    this.camera.setAspectRatio(GL.aspectRatio);
    this.cameraAR.setAspectRatio(GL.aspectRatio);

    if (this.webXRSystem) this.webXRSystem.resize(w, h);
    this._vHud.resize(window.innerWidth, window.innerHeight);
    this._vARDetail.resize(window.innerWidth, window.innerHeight);
  }

  get isAR() {
    return this.webXRSystem && this.webXRSystem.active;
  }
  get viewMatrix() {
    const transform = this.cameraAR.transform;

    if (!transform) return null;

    return transform.matrix;
  }

  /**
   * CALLED FROM MAIN APP
   */
  stop() {
    super.stop();
    this.pause();
    this.active = false;
  }

  start() {
    super.start();
    this.resume();
    this.active = true;
  }

  pause() {
    if (this.paused) return;
    this.paused = true;
    this.previousState = {
      orbitalControlConnected: this.orbitalControl.connected,
      controlsProductConnected: this.controlsProduct.connected,
      touchSystemConnected: this.touchSystem.connected,
    }

    this.touchSystem.disconnect();
    this.orbitalControl.disconnect();
    this.controlsProduct.disconnect();
    
  }

  resume() {
    this.paused = false;
    if (this.previousState.touchSystemConnected) this.touchSystem.connect();
    if (this.previousState.orbitalControlConnected) this.orbitalControl.connect();
    if (this.previousState.controlsProductConnected) this.controlsProduct.connect();
  }

  /**
   * DEBUG
   * */

  updateBallCenter(pos) { // editor debug
    this.positionCenterDebug = pos;
  }

  updateBallPos(pos) { // editor debug
    this.positionDebug = pos;
  }
}

export default SceneApp;
