// ViewCube.js

import alfrid, { GL } from 'alfrid';
import vs from 'shaders/black-bg.vert';
import fs from 'shaders/black-bg.frag';

let ind = 0;

const tempVec3 = vec3.create();

export class ViewARBlackBG extends alfrid.View {
  constructor(scene) {
    super(vs, fs);
    this.scene = scene;

    this.mesh = alfrid.Geom.sphere(50, 10, true);
  }

  
  render() {
    this.shader.bind();
    GL.draw(this.mesh);
  }
}
