// ViewCube.js

import alfrid, { GL } from 'alfrid';

import Assets from '../../Assets';
import Config from '../Config';
import { Sprite } from 'helpers';
import fs from 'shaders/sprite.frag';
import gsap from 'gsap';
import vs from 'shaders/sprite.vert';

let ind = 0;

const tempVec3 = vec3.create();

export class ViewARDetail extends alfrid.View3D {
  constructor(scene) {
    super(vs, fs);
    this.scene = scene;

    this.mainTexture = Assets.get('hotspots_img');
    this.mainTexture.texture.minFilter = GL.gl.NEAREST;
    this.mainTexture.texture.magFilter = GL.gl.NEAREST;
    this.active = false;

    // this.shaderSprite = new alfrid.GLShader(vs, fs)
    this.shader.bind();
    this.shader.uniform('texture', 'uniform1i', 0);

    // this.bg = new Sprite('pixel-black');
    this.imgBg = new Sprite('pixel-white');
    this.closeIcon = new Sprite('close-icon');

    this.imgHotspot = new Sprite();
    this.imgText = new Sprite();

    this.mesh = alfrid.Geom.plane(1, 1, 1);
  }

  start() {
    this.active = true;
    this.imgText.alpha = 0;
    this.imgText.x = -this.width / 4;

    const height = this.height * Config.ARDetails.white.height;
    const y = -this.height / 2 + height / 2;

    this.imgBg.y = y - height;
    this.imgHotspot.y = this.imgBg.y;

    gsap.to([this.imgBg, this.imgHotspot], 0.5, {
      y,
      ease: 'sine.out',
    });

    gsap.to(this.imgText, 0.3, {
      delay: 0.2,
      x: 0,
      alpha: 1,
      ease: 'sine.out',
    });
  }

  stop() {
    this.active = false;
  }

  animateOut() {
    return new Promise((resolve) => {
      // gsap.to(obj, duration, {
      // 		prop: 1,
      // 		onComplete: () => {
      // 				resolve()
      // 		},
      // })

      resolve();
    })
  }

  setData({ data }) {
    this.imgHotspot.setTexture(`${data.indexImg}-laptop`);
    this.imgText.setTexture(`${data.indexImg}-copy`);

    this.fitWidth = data.fitWidth;

    this.resize(window.innerWidth, window.innerHeight);
  }

  render() {
    // if (!this.active) return;

    // const posCamera = this.scene.touchSystem.positionCamera;
    // const dirCamera = this.scene.touchSystem.dirCamera;

    // const dist = 1.5;

    // this.x = posCamera[0] + dirCamera[0] * dist;
    // this.y = posCamera[1] + dirCamera[1] * dist;
    // this.z = posCamera[2] + dirCamera[2] * dist;

    GL.disable(GL.DEPTH_TEST);
    this.shader.bind();
    this.mainTexture.bind(0);

    // this.bg.updateShader(this.shader);
    // GL.draw(this.mesh);

    this.imgBg.updateShader(this.shader);
    GL.draw(this.mesh);

    this.imgHotspot.updateShader(this.shader);
    GL.draw(this.mesh);

    this.imgText.updateShader(this.shader);
    GL.draw(this.mesh);

    this.closeIcon.updateShader(this.shader);
    GL.draw(this.mesh);


    GL.enable(GL.DEPTH_TEST);
  }

  resize(w, h) {
    this.width = w;
    this.height = h;

    // this.bg.setScale(window.innerWidth, window.innerHeight, 1);

    let height = h * Config.ARDetails.white.height;

    this.imgBg.setScale(window.innerWidth, height, 1);
    this.imgBg.setPosition(0, -h / 2 + height / 2, 0);
    
    this.closeIcon.setScale(40, 40, 1);
    this.closeIcon.setPosition(window.innerWidth / 2 - 40, window.innerHeight / 2 - 60, 0);

    // height = h / 4;
    let width = this.imgHotspot.ratio * height
    
    if (this.fitWidth) {
      width = window.innerWidth;
      height = width / this.imgHotspot.ratio;
    }
    // let width = this.imgHotspot.ratio * height;

    this.imgHotspot.setScale(width, height, 1);
    this.imgHotspot.setPosition(0, this.imgBg.y, 0);

    if (w > h) {
      height = h / 6;
      width = this.imgText.ratio * height;
    }
    else {
      width = w * 0.9;
      height = w / this.imgText.ratio;
    }

    this.imgText.setScale(width, height, 1);
    const extra = this.fitWidth ? this.imgText.scaleY / 1.2 : this.imgText.scaleY / 3 * 2; // some magic number :shrug:
    this.imgText.setPosition(0, this.imgBg.y + this.imgBg.scaleY / 2 + extra, 0);
  }
}
