import { StateDefault } from './StateDefault';
import gsap from 'gsap';

export class StateARMain extends StateDefault {
  begin({ from }) {
    this.scene._vReticle.stop();
    this.product = this.scene._vProduct;
    this.hotspots = this.scene._vHotSpots;

    this.webXRSystem = this.scene.webXRSystem;
    this.reticle = this.scene._vReticle;

    this.scene._vHud.setState(99999);

    if (from === 'ARScan') {
      this.product.animateIn(() => {
        this.hotspots.updateMatrices(this.product._matrix);
        
        this.scene._vHud.setState(1);
        
        gsap.delayedCall(3, () => {
          this.hotspots.start();
          this.scene._vHud.setState(2);
  
          gsap.delayedCall(5, () => {
            this.scene._vHud.setState(9999);
          })
        });
      });
    }
    

    this.webXRSystem.on('onDown', this.onARDown.bind(this));
  }

  onARDown(pos) {
    const hits = this.scene.touchSystem.hit(pos);

    if (hits && hits[0]) {
      // console.log(`click on spot index ${hits[0].index} = ${this.scene._vHotSpots.spots[hits[0].index]}`);
      this.scene.statesSystem.set('ARDetail', { hotspot: this.scene._vHotSpots.spots[hits[0].index] });
    }
  }

  end() {
    this.scene._vHud.setState(9999);
    this.webXRSystem.off('onDown', this.onARDown.bind(this));
  }
}
